import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./contact-page-content.module.scss"
import treeWithLightImage from "../images/treeWithLight.png"

export default () => {
  const {
    pine,
    iconPhone,
    iconMail,
    iconSkype,
    catFace,
  } = useStaticQuery(graphql`
    query {
      pine: file(relativePath: { eq: "pine.png" }) {
        childImageSharp {
          fixed(height: 80) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconPhone: file(relativePath: { eq: "iconPhone.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconMail: file(relativePath: { eq: "iconMail.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconSkype: file(relativePath: { eq: "iconSkype.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      catFace: file(relativePath: { eq: "catFace.png" }) {
        childImageSharp {
          fixed(width: 54) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <div className={styles.container}>
      <div className={styles.mainContent}>
        <div className={styles.headingSection}>
          <Img fixed={pine.childImageSharp.fixed} alt="" />
          <div className={styles.headingContainer}>
            <div>Let's Make</div>
            <div>Something Great</div>
            <div>Together</div>
          </div>
          <Img
            fixed={pine.childImageSharp.fixed}
            alt=""
            imgStyle={{
              WebkitTransform: "scaleX(-1)",
              transform: "scaleX(-1)",
            }}
          />
        </div>
        <div className={styles.contactInfoSection}>
          <div className={styles.contactInfo}>
            <Img fixed={iconPhone.childImageSharp.fixed} alt="" />
            <div className={styles.contactInfoText}>(+84)962.61.81.22</div>
          </div>
          <div className={styles.contactInfo}>
            <Img fixed={iconSkype.childImageSharp.fixed} alt="" />
            <div className={styles.contactInfoText}>hanguyen459</div>
          </div>
          <div className={styles.contactInfo}>
            <Img fixed={iconMail.childImageSharp.fixed} alt="" />
            <div className={styles.contactInfoText}>ntbichha95@gmail.com</div>
          </div>
        </div>
        <div className={styles.noticeSection}>
          <div className={styles.noticeText}>
            Please feel free. I don't scratch.
          </div>
          <Img fixed={catFace.childImageSharp.fixed} alt="" />
        </div>
        <div className={styles.treeSection}>
          <img src={treeWithLightImage} alt="" className={styles.treeImage} />
        </div>
      </div>
    </div>
  )
}
