import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./about-page-content.module.scss"
import EmptyLine from "./empty-line"

export default () => {
  const {
    pine,
    avatar,
    travelMyWorld,
    raiseCats,
    createThings,
    toolPs,
    toolFigma,
    toolAi,
    toolAe,
  } = useStaticQuery(graphql`
    query {
      pine: file(relativePath: { eq: "pine.png" }) {
        childImageSharp {
          fixed(height: 80) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      avatar: file(relativePath: { eq: "avatar.png" }) {
        childImageSharp {
          fixed(width: 200) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      travelMyWorld: file(relativePath: { eq: "travelMyWorld.png" }) {
        childImageSharp {
          fixed(height: 228) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      raiseCats: file(relativePath: { eq: "raiseCats.png" }) {
        childImageSharp {
          fixed(height: 228) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      createThings: file(relativePath: { eq: "createThings.png" }) {
        childImageSharp {
          fixed(height: 228) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      toolPs: file(relativePath: { eq: "toolPs.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      toolFigma: file(relativePath: { eq: "toolFigma.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      toolAi: file(relativePath: { eq: "toolAi.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      toolAe: file(relativePath: { eq: "toolAe.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <div className={styles.pageContainer}>
      <div className={styles.section}>
        <div className={styles.headingContent}>
          <Img fixed={pine.childImageSharp.fixed} alt="" />
          <div className={styles.pageHeading}>About Me</div>
          <Img
            fixed={pine.childImageSharp.fixed}
            alt=""
            imgStyle={{
              WebkitTransform: "scaleX(-1)",
              transform: "scaleX(-1)",
            }}
          />
        </div>
      </div>
      <EmptyLine />
      <div className={styles.section}>
        <div className="columns is-gapless">
          <div className={styles.avatarContainer}>
            <Img fixed={avatar.childImageSharp.fixed} alt="" />
          </div>
          <div>
            <div className={styles.heading}>Hello! I’m Hannah Nguyen.</div>
            <div>
              <div>
                I’m an ordinary girl striving to add every little&nbsp;
                <strong>extra</strong> to become <strong>extraordinary</strong>.
              </div>
              <EmptyLine variant="Paragraph" />
              <div>
                When I fall into an unfavorable situation, the first thing comes
                to my mind is "
                <strong>
                  How to get out of this as soon and efficiently as possible?
                </strong>
                ", instead of "<strike>Why me</strike>?".
              </div>
            </div>
          </div>
        </div>
      </div>
      <EmptyLine />
      <div className={styles.section}>
        <div className={styles.heading}>I design web and mobile.</div>
        <div>
          I’m passionate about&nbsp;<strong>UI/UX design</strong>. Not aiming to
          be just a pixel pusher, I attempt to become an expert in web and
          mobile design.
        </div>
        <EmptyLine variant="Paragraph" />
        <div className={styles.proficiencyContainer}>
          <div className={styles.proficiencyLabel}>My proficiency:</div>
          <Img
            fixed={toolPs.childImageSharp.fixed}
            alt=""
            className={styles.proficiencyTool}
          />
          <Img
            fixed={toolFigma.childImageSharp.fixed}
            alt=""
            className={styles.proficiencyTool}
          />
          <Img
            fixed={toolAi.childImageSharp.fixed}
            alt=""
            className={styles.proficiencyTool}
          />
          <Img
            fixed={toolAe.childImageSharp.fixed}
            alt=""
            className={styles.proficiencyTool}
          />
        </div>
      </div>
      <EmptyLine />
      <div className={styles.section}>
        <div className="columns is-gapless">
          <div className="column has-text-left">
            <div className={styles.heading}>I travel my world.</div>
            <EmptyLine variant="Paragraph" />
            <Img fixed={travelMyWorld.childImageSharp.fixed} alt="" />
          </div>
          <div className="column has-text-centered">
            <div className={styles.heading}>I raise cats.</div>
            <EmptyLine variant="Paragraph" />
            <Img fixed={raiseCats.childImageSharp.fixed} alt="" />
          </div>
          <div className="column has-text-right">
            <div className={styles.heading}>I create things.</div>
            <Img fixed={createThings.childImageSharp.fixed} alt="" />
          </div>
        </div>
      </div>
      <EmptyLine />
    </div>
  )
}
