import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"
import AniLink from "gatsby-plugin-transition-link/AniLink"

import styles from "./my-work-page-content.module.scss"
import ButtonLink from "./button-link"
import EmptyLine from "./empty-line"

export default () => {
  const {
    pine,
    mockupDreamWork,
    mockupHippoMeowV1,
    mockupHippoMeowV2,
    mockupMicroLector,
    mockupTitanNews,
    iconArrowGreen,
    iconArrowYellow,
  } = useStaticQuery(graphql`
    query {
      pine: file(relativePath: { eq: "pine.png" }) {
        childImageSharp {
          fixed(height: 80) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupDreamWork: file(relativePath: { eq: "mockupDreamWork.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupHippoMeowV1: file(relativePath: { eq: "mockupHippoMeowV1.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupHippoMeowV2: file(relativePath: { eq: "mockupHippoMeowV2.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupMicroLector: file(relativePath: { eq: "mockupMicroLector.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupTitanNews: file(relativePath: { eq: "mockupTitanNews.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconArrowGreen: file(relativePath: { eq: "iconArrowGreen.png" }) {
        childImageSharp {
          fixed(width: 60) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconArrowYellow: file(relativePath: { eq: "iconArrowYellow.png" }) {
        childImageSharp {
          fixed(width: 60) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.headingContainer}>
        <div className={styles.headingContent}>
          <Img fixed={pine.childImageSharp.fixed} alt="" />
          <div className={styles.heading}>My Work</div>
          <Img
            fixed={pine.childImageSharp.fixed}
            alt=""
            imgStyle={{
              WebkitTransform: "scaleX(-1)",
              transform: "scaleX(-1)",
            }}
          />
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundWhite}`}>
        <div className={styles.projectRowContent}>
          <Img fixed={mockupDreamWork.childImageSharp.fixed} alt="" />
          <div className={styles.projectInfoSeparator} />
          <div className={styles.projectInfo}>
            <div>
              I create a website for posting and seeking jobs in design field.
              The main color is used based on 2020 color trend: classic blue.
              This color certainly brings a feeling of trust and gentleness.
            </div>
            <AniLink
              fade
              className={styles.projectCtaLink}
              to="/my-work/dreamwork"
            >
              <Img fixed={iconArrowGreen.childImageSharp.fixed} alt="" />
              <span className={styles.projectCtaLinkText}>More Details</span>
            </AniLink>
          </div>
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundYellow}`}>
        <div className={styles.projectRowContent}>
          <div className={styles.projectInfo}>
            <div>
              This is the homepage of an online newspaper. It is a big challenge
              for alignment and content arrangement.
            </div>
            <AniLink
              fade
              className={styles.projectCtaLink}
              to="/my-work/titan-news"
            >
              <Img fixed={iconArrowGreen.childImageSharp.fixed} alt="" />
              <span className={styles.projectCtaLinkText}>More Details</span>
            </AniLink>
          </div>
          <div className={styles.projectInfoSeparator} />
          <Img fixed={mockupTitanNews.childImageSharp.fixed} alt="" />
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundGreen}`}>
        <div className={styles.projectRowContent}>
          <Img fixed={mockupMicroLector.childImageSharp.fixed} alt="" />
          <div className={styles.projectInfoSeparator} />
          <div className={styles.projectInfo}>
            <div>
              I redesign an existing education websign homepage. Service logos
              and details, as well as attractive information, have been added.
            </div>
            <AniLink
              fade
              className={styles.projectCtaLink}
              to="/my-work/micro-lector"
            >
              <Img fixed={iconArrowYellow.childImageSharp.fixed} alt="" />
              <span
                className={`${styles.projectCtaLinkText} ${styles.projectCtaLinkTextYellow}`}
              >
                More Details
              </span>
            </AniLink>
          </div>
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundWhite}`}>
        <div className={styles.projectRowContent}>
          <div className={styles.projectInfo}>
            <div>
              I work on an idea for a family cake and cafe shop website. I aim
              to bring a fresh and friendly atmosphere for the homepage.
            </div>
            <AniLink
              fade
              className={styles.projectCtaLink}
              to="/my-work/hippo-meow-v1"
            >
              <Img fixed={iconArrowGreen.childImageSharp.fixed} alt="" />
              <span className={styles.projectCtaLinkText}>More Details</span>
            </AniLink>
          </div>
          <div className={styles.projectInfoSeparator} />
          <Img fixed={mockupHippoMeowV1.childImageSharp.fixed} alt="" />
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundYellow}`}>
        <div className={styles.projectRowContent}>
          <Img fixed={mockupHippoMeowV2.childImageSharp.fixed} alt="" />
          <div className={styles.projectInfoSeparator} />
          <div className={styles.projectInfo}>
            <div>
              I want to bring a traditional and cozy atmosphere for the users,
              so I let the homepage imitate the interface of a shop in reality
              with wood walls, lean-to, neon signboard, and window.
            </div>
            <AniLink
              fade
              className={styles.projectCtaLink}
              to="/my-work/hippo-meow-v2"
            >
              <Img fixed={iconArrowGreen.childImageSharp.fixed} alt="" />
              <span className={styles.projectCtaLinkText}>More Details</span>
            </AniLink>
          </div>
        </div>
      </div>
      <EmptyLine />
      <div className={styles.moreWorksSection}>
        <div className={styles.moreWorksSectionContent}>
          <div className={styles.moreWorksGreenLine} />
          <div className={styles.moreWorksButtonContainer}>
            <ButtonLink to="/404" title="More Works" />
          </div>
          <div className={styles.moreWorksGreenLine} />
        </div>
      </div>
      <EmptyLine />
    </React.Fragment>
  )
}
